body {
  margin: 0px;
  padding: 0px;
  background: url(https://images.pexels.com/photos/695657/pexels-photo-695657.jpeg?cs=srgb&dl=pexels-johann-piber-695657.jpg&fm=jpg);
}
.header-bg {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(auto);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 350px;
  height: auto;
  border-radius: 10px solid black;
  font-family: "OpenSans-Bold";
  font-size: larger;
  border: 5px solid black;
  border-radius: 8px;
  box-shadow: 4px 4px  rgba(245, 238, 238, 0.877);
  
}

.title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  color: rgb(19, 18, 18);
  font-size: 30px;
}
.input-text {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}
#city-name {
  padding: 6px;
  border-radius: 5px;
  font-size: 20px;
  background: rgb(231, 247, 247);
  border: 2px solid black;
  /* height: 35px; */
}
.search-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
}
.search-btn button {
  padding: 6px;
  background-color: #757575;
  font-family: "Patua One";
  font-weight: bolder;
  font-size: 20px;
  border-radius: 6px;
  width: 50%;
}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo img {
  width: 100px;
  height: 90px;
  margin: 5px;
}
.cityName {
  font-size: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.temp {
  font-size: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}
